import Glide from "@glidejs/glide";
import axios from "axios";

new Glide(".glide", {
  type: "carousel",
  startAt: 0,
  perView: 1,
  autoplay: 10000,
  gap: 15,
}).mount();

// Nav

let navToggle = document.querySelector(".nav-toggle");
let nav = document.querySelector(".nav");

navToggle.addEventListener("click", (e) => {
  navToggle.classList.toggle("nav-toggle_active");
  if (nav) {
    nav.classList.toggle("nav_active");
    document.body.classList.toggle("overflow");
  }
});

//smoth nav for ancore

let desktopNav = document.querySelector(".desktop-nav");

if (desktopNav) {
  desktopNav.addEventListener("click", (e) => {
    if (e.target.classList.contains("desktop-nav__link")) {
      e.preventDefault();
      let sectionId = e.target.getAttribute("href");
      let section = document.querySelector(sectionId);
      section.scrollIntoView({ behavior: "smooth" });
    }
  });
}

let footerNav = document.querySelector(".footer__menu");

if (footerNav) {
  footerNav.addEventListener("click", (e) => {
    if (e.target.classList.contains("footer__link")) {
      e.preventDefault();
      let sectionId = e.target.getAttribute("href");
      let section = document.querySelector(sectionId);
      section.scrollIntoView({ behavior: "smooth" });
    }
  });
}

let mobNav = document.querySelector(".nav");

if (mobNav) {
  mobNav.addEventListener("click", (e) => {
    if (e.target.classList.contains("nav__link")) {
      e.preventDefault();
      let sectionId = e.target.getAttribute("href");
      let section = document.querySelector(sectionId);
      section.scrollIntoView({ behavior: "smooth" });
      document.body.classList.toggle("overflow");
      navToggle.classList.toggle("nav-toggle_active");
      nav.classList.toggle("nav_active");
    }
  });
}

//Fix mobile url bar jump

let vh = window.innerHeight * 0.01;
document.documentElement.style.setProperty("--vh", `${vh}px`);

window.addEventListener("resize", () => {
  // We execute the same script as before
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);
});

